/*
 * RovenBlog WordPress Customizer CSS customizations
 */
 
 	#accordion-section-rovenblog_discover_pro {
		border-top: 15px solid #F0F0F1;
		padding: 15px 12px;
	}
	
	.rb-customize-section-description {
		display: block;
		margin-bottom: 15px;
	}
	
	#accordion-section-themes .accordion-section-title {
		margin-bottom: 0 !important;
	}

	.rb-title-section {
		display: block !important;
		border-top: 15px solid #F0F0F1;
		border-bottom: none !important;
		background-color: #fff;
	}

	.rb-title-section h3 {
		margin: 0;
		border-top: 1px solid #dcdcde;
		border-bottom: 1px solid #dcdcdc;
		padding: 8px 0 8px 18px !important;
		text-transform: uppercase;
		font-size: 90% !important;
		font-weight: bold;
	}

	#customize-outer-theme-controls .accordion-section-title,
	#customize-theme-controls .accordion-section-title {
		border-bottom: 0 !important;
		background-color: #fff;
	}
	
	.control-section .accordion-section-title::after,
	.customize-pane-child .accordion-section-title::after {
		top: calc(50% - 8px);
		font-size: 14px;
	}
	
	.customize-pane-parent>li.control-section .accordion-section-title::before {
		content: "";
		width: 18px;
		height: 18px;
		margin-top: -4px;
		margin-right: 4px;
		background-repeat: no-repeat;
		display: inline-block;
		vertical-align: middle;
		background-image: url(../svg/customizer/default.svg);
		background-size: contain;
	}

	#accordion-section-rovenblog_theme_settings .accordion-section-title::before {
		background-image: url(../svg/customizer/settings.svg);
	}

	#accordion-section-rovenblog_color_settings .accordion-section-title::before {
