	.control-section,
	#customize-theme-controls {
		background-color: #fff !important;
	}
	
	.customize-section-description-container.section-meta + .customize-control-rovenblog-separator-control {
		margin-top: -15px;
	} 
	
	.customize-control-rovenblog-separator-control h3 {
		text-transform: uppercase;
		font-size: 90% !important;
		font-weight: bold;
		border-top: 12px solid #F0F0F1;
		padding: 10px 0 10px 12px !important;
		border-bottom: 1px solid #dcdcdc;
		margin: 0 -12px;
	}

	.customize-control-rovenblog-separator-control hr {
		border: none;
		border-top: 12px solid #F0F0F1;
		margin: 10px -12px 10px -12px;
	}
	
	.customize-control-kirki-radio-image input+label img {
		border: 6px solid #E9EBEC;
		border-bottom: 0 !important;
		border-radius: 4px;
		margin-bottom: 6px;
		box-shadow: none !important;
		box-sizing: border-box !important;
	}

	.customize-control-kirki-radio-image input:checked+label img {
		border: 6px solid #3498db !important;
		border-bottom: 0 !important;
		box-shadow: none !important;
	}

	.customize-control-kirki-radio-image .customize-control-title {
		margin-bottom: 20px;
	}

